/* mobile menu function */
export const heightFix = () => {
    const appHeight = () => {
        const doc = document.documentElement
        doc.style.setProperty('--app-height', `${window.innerHeight}px`)
    }
    const headerHeight = () => {
        const doc = document.documentElement,
              height = document.querySelector('.cheader').offsetHeight;

        doc.style.setProperty('--header-height', `${height}px`)
    }
    const headerHalfHeight = () => {
        const doc = document.documentElement,
            height = document.querySelector('.cheader__top').offsetHeight;

        doc.style.setProperty('--headerhalf-height', `${height}px`)
    }
    window.addEventListener('resize', appHeight);
    window.addEventListener('resize', headerHeight);
    window.addEventListener('resize', headerHalfHeight);
    appHeight();
    headerHeight();
    headerHalfHeight();
}
