// modal
// import MicroModal from 'micromodal';

import intlTelInput from 'intl-tel-input';
// functions
import { heightFix } from './_func/_nojq/heightFix.js';
import { headerScrollChange } from './_func/_jq/headerScrollChange.js';
import { hamburger } from './_func/_jq/humburger.js';


document.addEventListener('DOMContentLoaded', () => {
	/* modile height fix */
	heightFix();
	headerScrollChange('scroll', 0);
	$('.cheader').on('click', '.cheader-close', function (){
		$('.cheader .cheader__top').slideUp(400).addClass('removed');
		setTimeout(function (){
			heightFix();
		}, 500);
		// and set choose to cookie
	});

	/* mobile menu */
	hamburger('hamburger');
	$('.mmenu-list__sublink').click(function (){
		$(this).parent().find('.submenu-list').slideToggle('fast')
		$(this).parent().toggleClass('active');
	});

	/* if mobile - remove menu */
	function change_menu(){
		if(window.innerWidth <= 640){
			$('.cheader__main .mmenu .mmenu-list').append($('.cheader__main .profile-links .personal-block'));
		}
		else{
			if($('.cheader__main .mmenu .mmenu-list .personal-block').length > 0){
				$('.cheader__main .profile-links').append($('.cheader__main .mmenu .mmenu-list .personal-block'));
			}
		}
	}
	change_menu();
	window.addEventListener('resize', change_menu);


	var mainSlider = new Swiper('#mslider', {
		speed: 800,
		direction: 'horizontal',
		spaceBetween: 30,
		loop: true,
		pagination: {
			el: '.cmain-pagination',
			clickable: true,
		},
		breakpoints: {
			// when window width is >= 320px
			768: {
				spaceBetween: 0,
				direction: 'vertical',
			}
		}
	});
	const cardsSlider = new Swiper('#ccards-slider', {
		speed: 800,
		slidesPerView: 'auto',
		spaceBetween: 0,
		centeredSlides: true,
		allowTouchMove: true,
		navigation: {
			nextEl: '.ccards-block .slider-btn.next-slide',
			prevEl: '.ccards-block .slider-btn.prev-slide',
		},
	});
	const answersSlider = new Swiper('#canswers-slider', {
		speed: 800,
		slidesPerView: 3,
		spaceBetween: 30,
		allowTouchMove: true,
		navigation: {
			nextEl: '.canswers-block .slider-btn.next-slide',
			prevEl: '.canswers-block .slider-btn.prev-slide',
		},
		breakpoints: {
			// when window width is >= 320px
			769: {
				slidesPerView: 4
			},
			641: {
				slidesPerView: 3
			},
			421: {
				slidesPerView: 2
			},
			0: {
				slidesPerView: 1
			}
		}
	});

	/* phones select */
	document.querySelectorAll('.custom-phone').forEach(input => {
		var phoneInput = intlTelInput(input, {
			utilsScript: 'js/utils.js',
			separateDialCode: true,
			defaultCountry: 'auto',
			preferredCountries: ['ru'],
			customPlaceholder: function(selectedCountryPlaceholder, selectedCountryData) {
				return "Телефон";
			}
		});
	});

	/* other selectors */
	setTimeout(function() {
		$('select.custom-select').styler();
	}, 100);
	// choose select
	$('.choose-select').change(function (){
		var curTab = $(this).find('option:selected').val();
		$('.ccurses-block__tabs__item').removeClass('active');
		$('.ccurses-block__tabs__item[data-tab="'+ curTab +'"]').addClass('active');
	})

	/* learning team btns function */
	$('.clearn-team .track-btn').click(function (){
		$(this).parents('.content-wrapper').find('.track-wrapper').slideToggle('fast');
		if($(this).hasClass('active')){
			$(this).text($(this).attr('data-close'));
			$(this).removeClass('active');
		}
		else{
			$(this).text($(this).attr('data-open'));
			$(this).addClass('active');
		}
	});
	/* learning page programm slider */
	if(window.innerWidth <= 640){
		$('#programm-slider .mslider-wrapper').addClass('swiper-wrapper');
		setTimeout(function (){
			var programmSlider = new Swiper('#programm-slider', {
				speed: 800,
				slidesPerView: 1,
				spaceBetween: 20,
				loop: false,
				pagination: {
					el: '.clearn-programm__content .cprogramm-pagination',
					clickable: true,
				},
			});
		},300)
	}
	/* learning page questions */
	$('.clearn-questions').on('click', '.clearn-questions__content__item .item-title', function (){
		$(this).parent().toggleClass('active');
		$(this).siblings('.item-descr').slideToggle(300);
	});
	/* learning page audio */
	GreenAudioPlayer.init({
		selector: '.audio-block',
		stopOthersOnPlay: true,
		showDownloadButton: false,
		showTooltips: false
	});
	/* stop audio when close */
	$('.js-close-modal').click(function (){
		if($(this).attr('data-player') == 'stop'){
			if($(this).parent().find('.play-pause-btn').attr('aria-label') == 'Pause'){
				$(this).parent().find('.play-pause-btn').click();
			}
		}
	});
	/* modal text function */
	$('.message-show').on('click','.show-btn', function (){
		$(this).parents('.info-item').find('.message-block').toggleClass('show');
		$(this).toggleClass('open');
	})
	/* footer menu function */
	$('.cfooter__top__column .menu-title').click(function (){
		$(this).toggleClass('show');
		$(this).siblings('ul').slideToggle('fast');
	})
});