/* scroll header function */
export const headerScrollChange = (header_class, scroll_offset) => {
    $(window).scroll(function() {
        var scroll = $(this).scrollTop();
        if(scroll > scroll_offset) {
            $('.cheader').addClass(header_class);
            if($('.cheader .cheader__top').is(':visible')){
                $('.cheader .cheader__top').slideUp(400);
            }
        } else {
            $('.cheader').removeClass(header_class);
            if(!$('.cheader .cheader__top').hasClass('removed')){
                $('.cheader .cheader__top').slideDown(400);
            }
        }
        if(scroll > 900){
            $('.totop_btn').fadeIn();
        }
        else{
            $('.totop_btn').fadeOut();
        }
    });
    $('.totop_btn').click(function (){
        $('html,body').stop().animate({ scrollTop: 0 }, 1000);
    });
}
